.navbar {
  background-color: #9ec3c6 !important;
  display: flex;
  align-items: center;
  width: 100vw;
}

.navbar-brand {
  font-size: 1rem;
  letter-spacing: 1px;
}

.fa-comment-dots {
  font-size: 27px;
  color: white;
  position: relative;
  z-index: 1;
}

.notification-num {
  background-color: red;
  border-radius: 100%;
  text-align: center;
  font-size: 0.8rem;
  width: 18px;
  height: 18px;
  position: absolute;
  bottom: 18px;
  left: 18px;
  margin: 0px;
  z-index: 2;
}

.logout-btn {
  cursor: pointer;
}

#chat-notification-icon {
  position: relative;
}

.navbar-toggler:focus {
  outline: none;
}

@media (min-width: 1024px) {
  .navbar-nav {
    display: flex;
    align-items: center;
  }

  .navbar {
    padding-left: 30px;
  }
}
